/**
 * Table
 */


.table-responsive {
  box-shadow: 20px 20px 10px #E8E8E8;
thead {
  background: #e4f3f8;
  color: #25282B;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 12px;
}
.table-responsive {
  color: #52575C;

}
thead a {
  color: #25282B;
}
}
.region {
  h1 {
  font-size: 2.9em;
  margin: 1em 0;
  letter-spacing: 0.4px;
}
}
.view-content{
  margin-top: 60px;
}
#edit-submit-frontpage{
  background-color: #f69 !important;
  color:white !important;
  border-color: #f69 !important;

}
.field.field--name-field-block.field--type-viewsreference.field--label-hidden.field--items .views-element-container.form-group{
display: flex !important;
justify-content:center;
margin-bottom: 60px;
margin-top: 60px;
background-color: #e4f3f8;
float:left;
margin-right: 80px;
width: 200px;
height: 100px;
border: 1px solid #9fa2a3;
border-radius: 25px;
 }
.form--inline.form-inline.clearfix{
margin-right: 4px;
}
input[type="date"].form-control, input[type="time"].form-control, input[type="datetime-local"].form-control, input[type="month"].form-control{
line-height: 18px !important;
}
.btn-info {
margin-left: 30px;
}
.form-group {
padding-right: 8px;
}
.links.btn-group.dropdown a {
  margin-right: 10px;
}
.links.btn-group.dropdown ul {
  display: inline-flex;
}
li{
  list-style: none;
}
